import React from 'react'
import Button from 'Shared/components/Button'
import './MessageToEmployerButton.scss'
import { t } from 'i18next'

const MessageToEmployerButton = ({ onClick, isDisabled }) => {
  return (
    <Button onClick={onClick} primary disabled={isDisabled} className={'message-to-employer-button'}>
      <i className={'icon-im-chat-conversations'} />
      {t('common.messageEmployer')}
    </Button>
  )
}

export default MessageToEmployerButton



