import React, { Component, Fragment } from 'react'
import PropTypes from 'prop-types'
import { Trans } from 'react-i18next'
import { Field, Form } from 'redux-form'
import { lengthLimit } from 'Shared/constants/Editor'
import withTranslations from 'Shared/hoc/withTranslations'
import { Editor, Select, Input } from 'Shared/components/FormElements'
import Divider from 'Shared/components/FormElements/Divider'
import ProfileStepSection from 'Shared/components/ProfileStepSection'
import AlertBox from 'Shared/components/AlertBox/AlertBox'
import Button, { PrimaryButton } from 'Shared/components/Button'
import withScripts from 'TrackingScripts'
import { not, isNil } from 'ramda'
import Notification from 'Shared/components/Notification/index'
import './JobOfferApplyForm.scss'
import { cleanseString } from 'Shared/helpers/string'
import OlivPlusSmartPopUpConnector from 'Candidate/connectors/OlivPlusSmartPopUpConnector/OlivPlusSmartPopUpConnector'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

class JobApplyFormConnector extends Component {
  static propTypes = {
    getApplicationIfApplied: PropTypes.func.isRequired,
    clearForm: PropTypes.func,
    closeOnSubmit: PropTypes.func,
    jobOffer: PropTypes.shape({
      id: PropTypes.string.isRequired,
      preferredVideoCv: PropTypes.bool.isRequired
    })
  }

  state = {
    isLoading: false
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (prevProps.isFormSubmitting !== this.props.isFormSubmitting) {
      this.setState({ isLoading: this.props.isFormSubmitting })
    }
  }

  getCoverLetterText = props => {
    const { jobOffer: { coverLetterRequired }, t } = props
    return coverLetterRequired
      ? <Trans i18nKey='jobOffer.jobDetailsDrawer.sections.coverLetter.text'>
        This employer has requested a cover letter. Please share why you're interested in this role, why you think you'd be the best fit, and anything else that will blow the employer away! If you need help, take a look at our
        <a href='https://oliv.com/blog/cover-letter' target='_blank'>cover letter guide.</a>
      </Trans>
      : t('jobOffer.jobDetailsDrawer.sections.coverLetter.textOptional')
  }

  get selectCvText () {
    const { t, jobOffer: { customCareerSiteUrl }, organisationName } = this.props
    const hasExternalLink = not(isNil(customCareerSiteUrl))

    return hasExternalLink
      ? <div>{t('jobOffer.jobDetailsDrawer.externalLink.sections.selectCv.text', { companyName: organisationName })}</div>
      : (
        <Trans i18nKey='jobOffer.jobDetailsDrawer.sections.selectCv.text'>
          Go ahead and attach your CV to apply! Remember to highlight your most relevant experience
          at the top. If you
          need help creating a CV,
          <a href='https://www.oliv.com/blog/cv-guide' target='_blank'>check out this guide!</a>
        </Trans>
      )
  }

  getButtonName = () => {
    const { t, jobOffer: { customCareerSiteUrl } } = this.props
    const hasExternalLink = not(isNil(customCareerSiteUrl))

    switch (true) {
      case hasExternalLink:
        return t('jobOffer.jobDetailsDrawer.externalLink.applyBtn')
      case this.state.isLoading:
        return `${t('common.loading')}...`
      default:
        return t('common.apply')
    }
  }

  profileCompletionNotification = () => {
    const { t, isCandidateProfileComplete, organisationName } = this.props
    const notificationText = () => (
      <Fragment>
        <div className='apply-form__complete-profile-notification-title'>{t('notifications.candidate.profileReview.title')}</div>
        <div className='apply-form__complete-profile-notification-description'>{cleanseString(t('notifications.candidate.profileReview.description', { organisationName }))}</div>
      </Fragment>
    )

    return isCandidateProfileComplete && (
      <div className='apply-form__complete-profile-notification-container'>
        <Notification
          messageText={notificationText()}
          iconClassName='icon-im-alert-circle-outline'
          warning
        />
      </div>
    )
  }

  handleSubmitWithBoost = async () => {
    await this.props.boostAfterApply()
    return this.props.submit()
  }

  render () {
    const { t, candidateFiles, isCandidateApproved, jobOffer: { customCareerSiteUrl } } = this.props
    const hasExternalLink = not(isNil(customCareerSiteUrl))
    return (
      <Form onSubmit={this.props.handleSubmit} className={'apply-form__form-container'}>
        {this.profileCompletionNotification()}
        {this.props.jobOffer.preferredVideoCv && <AlertBox
          title={t('jobOffer.jobDetailsDrawer.alerts.videoCv.title')}
          text={t('jobOffer.jobDetailsDrawer.alerts.videoCv.text')}
        />}
        {
          !hasExternalLink && (
            <Fragment>
              <ProfileStepSection
                label={t('jobOffer.jobDetailsDrawer.sections.coverLetter.label')}
                text={this.getCoverLetterText(this.props)}
                optional={!this.props.jobOffer.coverLetterRequired}
              />
              <div className='form__group'>
                <Field
                  component={Editor}
                  name='coverLetter'
                  limit={lengthLimit.jobOfferDescriptionMax}
                  placeholder={t('forms.coverLetter.placeholder')}
                />
              </div>
              <Divider />
            </Fragment>
          )
        }
        <ProfileStepSection
          label={hasExternalLink ? t('jobOffer.jobDetailsDrawer.externalLink.sections.selectCv.label') : t('jobOffer.jobDetailsDrawer.sections.selectCv.label')}
          text={this.selectCvText}
        />
        <div className='form__group'>
          <Field
            component={Select}
            withError
            options={candidateFiles}
            placeholder={t('forms.jobOfferCv.placeholder')}
            name='cvId'
            withIcon
          />
        </div>
        <div className='form__group'>
          <p>
            {t('candidate.applyForJob.shareFilesInfo')}
          </p>
        </div>
        {!this.props.isMobile && <div className='form__wrapper--drawer-actions'>
          <div className='form__wrapper--button-group apply-form-buttons'>
            <Button onClick={this.props.closeOnSubmit}>{t('common.cancel')}</Button>
            {(isCandidateApproved && !this.props.isExternalJobOffer) && <OlivPlusSmartPopUpConnector
              type={'confirm'}
              service={'boost'}
              customSubmit={this.handleSubmitWithBoost}
              triggerButton={<Button disabled={this.state.isLoading} withIcon iconClassName={'icon-im-boost'} primary outline className={'form__wrapper--drawer-boost-button'}>{t('olivPlus.smartPopUp.confirmBoost.applyWithBoost')}</Button>}
            />}
            <PrimaryButton disabled={this.state.isLoading} type='submit'>{this.getButtonName()}</PrimaryButton>
          </div>
        </div>}
        <Field
          component={Input}
          type='hidden'
          name='coverLetterRequired'
        />
        <Field
          component={Input}
          type='hidden'
          name='shouldBoostApplication'
        />
      </Form>
    )
  }
}

export default withDeviceWidthDetector(withScripts(withTranslations(JobApplyFormConnector)))



